@import url(https://fonts.googleapis.com/css?family=Roboto);
@import url(https://fonts.googleapis.com/css?family=Raleway);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

button, button:visited, button:focus, button:active, button:hover{
    outline:0 none !important;
}
body{
    background: #F8F8F8;
}
.main-container{
    /*background: red;*/
    margin-top: 15ch;
    grid-template-rows: 1fr;
    width: 100%;
    height: 100%;
    grid-row-gap: 40px;
}
#coursesContainer{
    /*background: blue;*/
    display: grid;
    width: 60%;
    margin-left: 20%;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr;
    grid-gap: 50px;
}
#coursesBlock{
    background: #FFFFFF;
    box-shadow: 0px 15px 20px rgba(187, 187, 187, 0.25);
    display: grid;
    grid-template-columns: 44% 56%;
    grid-template-rows: 1fr;
}
.photoPart img{
    width: 100%;
    height: 100%;
    margin-bottom: -2.5%;
}
.titleText{
    font-family: Roboto;
    font-style: normal;
    font-weight: 900;
    font-size: 16px;
    line-height: normal;
    text-transform: uppercase;
    color: #202020;
    margin-left: 1.2%;
    margin-top: 1%;
    position: absolute;
}
.extraText{
    position: absolute;
    font-family: Roboto;
    font-style: normal;
    font-weight: normal;
    font-size: 12px;
    line-height: normal;
