    font-size: 14px;
    color: #202020;
}
.detailButton{
    width: 60%;
    position: relative;
    height: 15%;
    margin-top: 12%;
    right: 22%;
    color: #ffffff;
    background: #32B482;
    box-shadow: 0 10px 20px rgba(50, 180, 130, 0.25);
    border-radius: 5px;
    border: none;
    font-family: Raleway;
    font-style: normal;
    font-weight: 800;
    font-size: 12px;
    line-height: normal;
    text-align: center;
    color: #FFFFFF;
}
