* {
	box-sizing: border-box;
    padding: 0;
    outline: none;
	font-family: Roboto;
}

html,
body,
.wrapper {
	width: 100%;
	height: 100%;
	font-size: 16px;
	margin: 0;
	padding: 0;
	color: #1f1f24;
}


.wrapper {
	overflow-x: hidden;
	position: relative;
	margin: 0px auto;
	max-width: 1100px;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-flow: column nowrap;
	        flex-flow: column nowrap;
}


/* Header */
a {
	text-decoration: none;
	color: white;
}

ul,ol {
	list-style: none;
	padding: 0px;
}

.active {
	background: #32B482 !important;
	box-shadow: 0px 10px 20px rgba(50, 180, 130, 0.25);
	border-radius: 3px;
	color: white !important;
}


/*  main */

.main {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-flow: column nowrap;
	        flex-flow: column nowrap;
	-webkit-justify-content: space-around;
	        justify-content: space-around;
}


/* info */


.info {
	display: -webkit-flex;
	display: flex;
	padding: 40px 30px 40px 100px;
}

.info .title {
	font-family: Raleway;
	font-style: normal;
	font-weight: 900;
	font-size: 40px;
	line-height: normal;
	text-transform: uppercase;
	text-align: left;
}

.tags {
	height: 21.12px;
	font-weight: bold;
	font-size: 12.96px;
	text-align: center;
	padding: 5px 20px;
	margin-bottom: 100px;
	font-style: italic;
}

.description {
	font-size: 20px;
	line-height: 28px;
	margin-bottom: 10px;
	padding-right: 5px;
}


.btn {
	margin: 30px 0px;
	padding: 10px 20px;
	text-align: center;
	font-weight: 800;
	font-family: Raleway;
	font-size: 16px;
	border-radius: 5px;
	color: black;
}

.read-more {
background: linear-gradient(to top, #FFFFFF, #FFFFFF);
box-shadow: 0px 10px 20px rgba(187, 187, 187, 0.25);
border-radius: 5px;
background: linear-gradient(to top, #FFFFFF, #FFFFFF);
box-shadow: 0px 10px 20px rgba(187, 187, 187, 0.25);
border-radius: 5px;
	padding: 10px 25px;
}

.read-more a {
	color: black;
}

/* About-course */

.about-course {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-flow: column nowrap;
	        flex-flow: column nowrap;
	padding: 40px 30px 40px 100px;
}

.title {
	text-align: center;
	font-family: Raleway;
	font-weight: 900;
	font-size: 24px;
	line-height: 34px;
	text-align: center;
	text-transform: uppercase;
	word-wrap: break-word;
}

.about-course .description, .subtitle {
	text-align: center;
}

.subtitle {
	font-size: 18px;
	line-height: 25px;

	text-align: center;
}

.about-course  .stage-plan {
  counter-reset: myCounter;
  padding: 0px;
}

.about-course .stage-plan__item {
	font-weight: 400;
	font-size: 20px;
	color: #32B482;
	margin:  40px 0;
	list-style: none;
	background-color: white;
	box-shadow: 0px 10px 20px rgba(187, 187, 187, 0.25);
	padding-right: 30px;
	margin-left: 60px;
	line-height: 28px;
	padding-left: 10px;
	padding-top: 5px;
}


.about-course  .stage-plan__item::before {
  counter-increment: myCounter;
  content:"." counter(myCounter);
  color: white;
  background: black;
  display: inline-block;
  text-align: center;
  margin: 0px -20px 10px 0px;
  line-height: 40px;
  width: 30px;
  height: 40px;
  font-family: Roboto;
  font-weight: bold;
  font-size: 24.6px;
  line-height: 39px;
  position: relative;
  right: 70px;
  top: -5px;
}

.form-course {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-flow: column nowrap;
	        flex-flow: column nowrap;
	text-align: center;
	background-color: white;
	overflow-x: hidden;
	position: relative;
	margin: 0px auto;
	max-width: 1900px;
}

